<template>
  <div class="echarts-container">
    <div id="myChart" :style="{ width: chartWidth + 'px', height: chartHeight + 'px' }"></div>
  </div>
</template>

<script>

import * as echarts from 'echarts';

export default {
  data() {
    return {
      chartWidth: 800, 
      chartHeight: 600 
    };
  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      const myChart = echarts.init(document.getElementById('myChart'));

      // 配置柱状图数据和样式等选项
      const barOption = {
        title: {
          text: '一周销量',
          left: 'center'
        },
        tooltip: {
          trigger: 'axis', // 坐标轴触发提示框，显示更详细的数据信息
          axisPointer: {
            type: 'shadow' // 鼠标悬停时显示阴影效果
          }
        },
        xAxis: {
          type: 'category',
          data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: '销量',
            type: 'bar',
            data: [120, 200, 150, 80, 70, 110, 130]
          }
        ]
      };
      
      const option = {
       ...barOption,
      };

      // 为图表添加交互能力：启用缩放和平移功能（仅针对直角坐标系图表有效）
      myChart.on('click', function (params) {
        console.log('点击图表元素：', params);
      });
      myChart.on('mouseover', function (params) {
        console.log('鼠标悬停在图表元素上：', params);
      });
      myChart.setOption(option);
    }
  }
};
</script>

<style scoped>
.echarts-container {
  margin: 20px;
}
</style>